
import moment from 'moment'
import {_} from '@/libs/util'
import common from '@/libs/common.mixin'
import globals from '@/libs/globals.mixin'
import MyCanlendar from 'c%/mycanlendar/MyCanlendar'
import leaveModal from './schedule/leave-modal.vue'
import Tag from 'c%/Tag.vue'

import Card from 'vux/src/components/card/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Divider from 'vux/src/components/divider/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'

export default{
mixins: [common,globals],
components:{
Tag,
Card,
Divider,
Flexbox,
LoadMore,
leaveModal,
FlexboxItem,
MyCanlendar
},
data () {
return {

data: [],
events: [],
length: 5,
current_day: this.$route.query.datetime || moment(new Date()).format("YYYY-MM-DD"),
}
},
watch: {
'current_day': function (val) {
console.log('this.currentDay:'+this.currentDay)
this.data_uri = 'course_arranges?int_day='+this.currentDay
this.init_data()
}
},
mounted () {
this.data_uri = 'course_arranges?int_day='+this.currentDay
this.pageIndex = 0
this.getEvents()
this.loadMore()
},
methods: {
init_data () {
this.loading = true
this.dr().getData('course_arranges?int_day='+this.currentDay)
.then(data=>{
this.data = data.list
setTimeout(()=>{
this.loading = false
},500)
})
},
refreshData () {
this.loading = true
this.dr().refreshData('course_arranges?int_day='+this.currentDay)
.then(data=>{
this.data = data.list
setTimeout(()=>{
this.loading = false
},500)
})
},
leave (row) {
this.$refs['leave']
.set('info',row)
.show()
},
getEvents () {
this.$rest('course_arranges/get_course_day')
.get()
.success(data=>{
if(data.list.length>0){
data.list.forEach(d=>{
this.events.push(this.$filter('int_date')(d))
})
}
})
}
},
computed: {
currentDay () {
console.log('this.current_day:'+this.current_day)
let result = moment(this.current_day).format('YYYYMMDD')
return result
}
}
}



