
import moment from 'moment'
import util from '@/libs/util'
import modal from '@/libs/modal.mixin'
import common from '@/libs/common.mixin'
import globals from '@/libs/globals.mixin'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Popup from 'vux/src/components/popup/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

export default{
mixins: [modal,common,globals],
directives: {
TransferDom
},
components: {
Popup,
XButton
},
data () {
return {
info: {},
type: 1
}
},
computed: {
class_time () {
return util.sprintf('%s %s~%s',
moment(this.$filter('int_date')(this.info.int_day)).format('YYYY年MM月DD日'),
this.$filter('int_hour')(this.info.int_start_hour),
this.$filter('int_hour')(this.info.int_end_hour))
}
},
methods: {
radioChange (type) {
this.type = type
},
ok () {
let params = {
type: this.type,
sid: this.sid$,
ca_id: this.info.ca_id
}
this.$rest('student_leaves')
.post(params)
.success(data=>{
this.$emit('on-success')
this.close()
}).error(response=>{
this.alert('',response.body.message||'请假失败')
})

},
alert (title,content) {
return new Promise((resolve,reject)=>{

this.$vux.alert.show({
title: title||'提示',
content: content||'',
onShow () {

},
onHide () {
reject()
}
})
})
}
}
}



